
import React, { useRef, useEffect, useState } from 'react';
import { ScrollArea } from "@/components/ui/scroll-area";
import MessageBubble from './MessageBubble';
import TypingIndicator from './TypingIndicator';
import { ChatMessage } from '@/services/chatService';

interface MessageListProps {
  messages: ChatMessage[];
  isLoading: boolean;
  isExpanded: boolean;
}

const MessageList: React.FC<MessageListProps> = ({ 
  messages, 
  isLoading,
  isExpanded 
}) => {
  // Reference to scroll area for handling auto-scroll
  const scrollAreaRef = useRef<HTMLDivElement>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  
  // Track user scrolling to prevent auto-scroll interruption
  const [userIsScrolling, setUserIsScrolling] = useState(false);
  const [shouldAutoScroll, setShouldAutoScroll] = useState(true);
  const lastMessagesLength = useRef(messages.length);
  
  // Enhanced scrolling function with check if we should auto-scroll
  const scrollToBottom = () => {
    if (shouldAutoScroll && messagesEndRef.current && !userIsScrolling) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }
  };
  
  // Handle manual scrolling detection
  const handleScroll = (e: React.UIEvent<HTMLDivElement>) => {
    if (!scrollAreaRef.current) return;
    
    const { scrollTop, scrollHeight, clientHeight } = e.currentTarget;
    const isScrolledToBottom = Math.abs(scrollHeight - scrollTop - clientHeight) < 20;
    
    // Only set auto-scroll based on user position at bottom
    setShouldAutoScroll(isScrolledToBottom);
    
    // Reset user scrolling after a short delay
    if (userIsScrolling) {
      setTimeout(() => setUserIsScrolling(false), 150);
    }
  };
  
  // Set up scroll events to detect user scrolling
  useEffect(() => {
    const scrollElement = document.querySelector('[data-radix-scroll-area-viewport]');
    
    if (scrollElement) {
      const handleUserScroll = () => {
        setUserIsScrolling(true);
      };
      
      scrollElement.addEventListener('wheel', handleUserScroll, { passive: true });
      scrollElement.addEventListener('touchmove', handleUserScroll, { passive: true });
      
      return () => {
        scrollElement.removeEventListener('wheel', handleUserScroll);
        scrollElement.removeEventListener('touchmove', handleUserScroll);
      };
    }
  }, []);
  
  // Effect for scrolling to bottom only when messages change or loading state changes
  useEffect(() => {
    // Only automatically scroll if new messages were added
    const messagesAdded = messages.length > lastMessagesLength.current;
    lastMessagesLength.current = messages.length;
    
    if (messagesAdded || isLoading) {
      // Adding a slightly longer delay to ensure content is fully rendered
      const timeoutId = setTimeout(() => {
        scrollToBottom();
      }, 200);
      
      return () => clearTimeout(timeoutId);
    }
  }, [messages, isLoading]);

  return (
    <div className={`w-full mb-6 ${isExpanded ? 'flex-grow' : 'h-48'} overflow-hidden bg-transparent rounded-lg`}>
      <ScrollArea 
        className="h-full pr-4" 
        type="auto"
        scrollHideDelay={300}
      >
        <div 
          className="px-2 py-1" 
          ref={scrollAreaRef}
          onScroll={handleScroll}
        >
          {/* Empty message state */}
          {messages.length === 0 && (
            <div className="p-3 text-gray-200">
              <p className="text-sm">Начните диалог с ассистентом Амика, отправив текстовое или голосовое сообщение.</p>
            </div>
          )}
          
          {/* Rendered message list */}
          {messages.map((message, index) => {
            const uniqueKey = message.id || `message-${index}-${Date.now()}-${message.user_query?.substring(0, 10) || 'bot'}`;
            
            return (
              <div key={uniqueKey} className="mb-3 animate-fade-in">
                {/* User message */}
                {message.user_query && (
                  <MessageBubble 
                    content={message.user_query}
                    isUser={true}
                  />
                )}
                
                {/* Bot response */}
                {message.isLoading ? (
                  <div className="flex justify-start">
                    <div className="max-w-[80%] bg-gray-800/80 backdrop-blur-sm rounded-lg px-4 py-3 text-gray-100 shadow-md">
                      <TypingIndicator />
                    </div>
                  </div>
                ) : (
                  <MessageBubble 
                    content={message.bot_response}
                    isUser={false}
                  />
                )}
              </div>
            );
          })}

          {/* Loading indicator - simplified and cleaner */}
          {isLoading && !messages.find(m => m.isLoading) && (
            <div className="flex justify-start mb-3 animate-fade-in">
              <div className="inline-flex items-center bg-gray-800/80 backdrop-blur-sm rounded-lg px-3 py-2 shadow-md">
                <TypingIndicator />
              </div>
            </div>
          )}
          
          {/* This is the invisible element that we'll scroll to */}
          <div ref={messagesEndRef} className="h-px" /> {/* Added a small height to ensure it's rendered */}
        </div>
      </ScrollArea>
    </div>
  );
};

export default MessageList;
