
import React, { useState, useEffect } from 'react';
import { toast } from "@/components/ui/sonner";

interface VoiceCircleProps {
  isListening: boolean;
  isSpeaking: boolean;
  voiceVolume: number;
  onClick: () => void;
}

const VoiceCircle: React.FC<VoiceCircleProps> = ({
  isListening,
  isSpeaking,
  voiceVolume,
  onClick,
}) => {
  // State для плавных переходов анимации
  const [animationClass, setAnimationClass] = useState("");
  const [glowIntensity, setGlowIntensity] = useState("opacity-30");
  
  // Обработка плавных переходов для состояния анимации
  useEffect(() => {
    let timeout: NodeJS.Timeout;
    
    if (isSpeaking) {
      setAnimationClass("animate-pulse-wave");
      setGlowIntensity("opacity-70");
    } else {
      // Задержка удаления класса анимации для более плавного перехода
      timeout = setTimeout(() => {
        setAnimationClass("");
        setGlowIntensity("opacity-30");
      }, 500); // Плавное затухание
    }
    
    return () => {
      if (timeout) clearTimeout(timeout);
    };
  }, [isSpeaking]);
  
  // Масштабирование применяется только когда ассистент говорит
  const getScaleFactor = () => {
    if (!isSpeaking) return 1;
    // Non-linear scaling for better visual feedback
    // Quadratic scaling gives better visual feedback at higher volumes
    const baseScale = 1;
    const scaleFactor = voiceVolume * 0.04; // More pronounced scaling
    return baseScale + scaleFactor;
  };
  
  // Wave animation всегда активна внутри круга, но наружные волны только при говорении
  const getWaveIntensity = () => {
    if (isSpeaking) {
      if (voiceVolume > 7) return 'animate-wave-high';
      if (voiceVolume > 3) return 'animate-wave';
    }
    return 'animate-wave-low'; // Всегда имеем низкую анимацию внутри круга
  };
  
  const waveClass = getWaveIntensity();
  const scaleFactor = getScaleFactor();
    
  // Обработчик клика с проверкой состояния
  const handleClick = async () => {
    try {
      onClick();
    } catch (error) {
      console.error("Ошибка при активации голосового ассистента:", error);
      toast.error("Не удалось активировать голосовой ассистент");
    }
  };

  return (
    <div className="relative">
      {/* Enhanced glow effect beneath the circle - slow animated gradient shadow */}
      <div className={`absolute inset-0 scale-150 rounded-full blur-xl transition-opacity duration-700 ${glowIntensity}`}>
        <div className="absolute inset-0 rounded-full bg-gradient-radial from-blue-400 via-blue-300 to-transparent opacity-80 animate-pulse" 
             style={{animationDuration: '4s'}}></div>
      </div>
      
      {/* Main circle */}
      <div 
        className={`relative h-16 w-16 rounded-full bg-gradient-to-t from-blue-500 to-blue-200 shadow-lg cursor-pointer transition-all duration-200 ease-in-out`}
        onClick={handleClick}
        style={{ transform: `scale(${scaleFactor})` }}
      >
        {/* Wave элементы внутри круга - всегда анимированы */}
        <div className="absolute inset-0 opacity-90 rounded-full overflow-hidden">
          <div className="absolute bottom-0 left-0 right-0 h-1/2 bg-opacity-20">
            <div className={`absolute bottom-0 left-0 w-full h-5 bg-white opacity-20 rounded-full transform -translate-y-2 ${waveClass}`}></div>
            <div className={`absolute bottom-0 left-0 w-full h-4 bg-white opacity-30 rounded-full transform -translate-y-1 translate-x-2 ${waveClass}`}></div>
            <div className={`absolute bottom-0 left-0 w-full h-3 bg-white opacity-40 rounded-full transform ${waveClass}`}></div>
          </div>
        </div>
        
        {/* Enhanced glow effect when speaking */}
        {isSpeaking && (
          <div className="absolute -inset-4 rounded-full blur-md bg-gradient-to-r from-blue-400/40 via-blue-300/30 to-blue-200/20 animate-pulse" 
               style={{animationDuration: '2s'}}></div>
        )}
        
        {/* Индикатор активного микрофона */}
        {isListening && (
          <div className="absolute -top-1 -right-1 h-3 w-3 rounded-full bg-red-500 animate-pulse"></div>
        )}
      </div>
    </div>
  );
};

export default VoiceCircle;
