import React, { useState, useEffect, useRef } from 'react';
import { Dialog, DialogTrigger } from "@/components/ui/dialog";
import useVoiceRecognition from './voice/hooks/useVoiceRecognition';
import VoiceDialogTrigger from './voice/VoiceDialogTrigger';
import VoiceDialogContainer from './voice/VoiceDialogContainer';
import { useVoiceChat } from './voice/hooks/useVoiceChat';
import { useDialogPosition } from './voice/hooks/useDialogPosition';
import { useConversation } from '@11labs/react';
import { toast } from "@/components/ui/sonner";
import { supabase } from "@/integrations/supabase/client";

interface VoiceAssistantWidgetProps {
  embeddedMode?: boolean;
}

const VoiceAssistantWidget: React.FC<VoiceAssistantWidgetProps> = ({ 
  embeddedMode = false 
}) => {
  // Состояние диалога
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isExpanded, setIsExpanded] = useState(false);
  
  // Состояние голосового управления
  const [isListening, setIsListening] = useState(false);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [voiceVolume, setVoiceVolume] = useState(0);
  const [transcript, setTranscript] = useState("");
  
  // Ref для отслеживания процесса отправки
  const isAutoSendingRef = useRef(false);
  
  // Функция для форматирования номера телефона
  const formatPhoneNumber = (phone: string): string => {
    // Удаляем все нецифровые символы
    let cleaned = phone.replace(/\D/g, '');
    
    // Если номер начинается с 8, заменяем на 7 (для России)
    if (cleaned.startsWith('8') && cleaned.length === 11) {
      cleaned = '7' + cleaned.substring(1);
    }
    
    // Если номер не содержит код страны, добавляем 7 (для России)
    if (cleaned.length === 10) {
      cleaned = '7' + cleaned;
    }
    
    console.log(`Отформатированный номер телефона: ${cleaned}`);
    return cleaned;
  };
  
  // Генерация текста сообщения на основе контекста
  const generateMessageText = async (phone: string, context?: string): Promise<string> => {
    try {
      // Определяем имя гостя по номеру телефона
      let guestName = "";
      const { data: bookings } = await supabase
        .from("bookings")
        .select("guest_name")
        .eq("guest_phone", phone)
        .order("created_at", { ascending: false })
        .limit(1);

      if (bookings && bookings.length > 0) {
        guestName = bookings[0].guest_name;
      }

      // Формируем базовое приветствие
      let messageText = `Здравствуйте${guestName ? ", " + guestName : ""}!`;
      
      // Добавляем контекст, если он есть
      if (context) {
        if (context.toLowerCase().includes('музык')) {
          messageText += " Пожалуйста, выключите музыку. Спасибо за понимание.";
        } else if (context.toLowerCase().includes('шум')) {
          messageText += " Пожалуйста, соблюдайте тишину. Спасибо за понимание.";
        } else if (context.toLowerCase().includes('уборк')) {
          messageText += " Сообщаем, что скоро будет проведена уборка в вашем номере.";
        } else if (context.toLowerCase().includes('выезд') || context.toLowerCase().includes('выселени')) {
          messageText += " Напоминаем о выезде до 12:00. Если нужно продлить проживание, сообщите администратору.";
        } else {
          messageText += " Как мы можем вам помочь?";
        }
      } else {
        messageText += " Как мы можем вам помочь?";
      }

      return messageText;
    } catch (error) {
      console.error("Ошибка при генерации текста сообщения:", error);
      return "Здравствуйте! Как мы можем вам помочь?";
    }
  };
  
  // Кастомные хуки
  const { triggerRef, dialogPosition } = useDialogPosition({ 
    offsetTop: -600, 
    offsetLeft: -450,
    embedded: embeddedMode 
  });
  
  const { 
    messages, 
    isLoading, 
    userInput, 
    setUserInput, 
    loadChatHistory, 
    handleSendMessage,
    handleSendMessageText,
    sendWhatsAppMessage,
    createTask,
    manageBooking,
    getWhatsAppMessages
  } = useVoiceChat();
  
  // ElevenLabs conversation hook с реализацией clientTools
  const conversation = useConversation({
    onConnect: () => {
      // Используется Agent ID:
      console.log("Используется Agent ID:", process.env.NEXT_PUBLIC_AGENT_ID || "bk6zd2EX7Rc7o9LnxEtY");
      console.log("ElevenLabs conversation connected");
      setIsListening(true);
      toast.success("Голосовой ассистент активирован");
    },
    onDisconnect: () => {
      console.log("ElevenLabs conversation disconnected");
      setIsListening(false);
      setIsSpeaking(false);
    },
    onMessage: (message) => {
      // Обработка транскрипций (речь пользователя)
      if (message.type === 'transcription' && message.transcription?.is_final) {
        console.log("Распознанная речь:", message.transcription.text);
        setTranscript(message.transcription.text);
      }
      // Обработка ответов AI
      else if (message.type === 'generation' && message.generation?.text) {
        console.log("Ответ ассистента:", message.generation.text);
        setIsSpeaking(true);
      }
      // Отладочные сообщения
      else if (message.type === 'debug') {
        console.log("ElevenLabs debug message:", message.debug);
      }
    },
    onError: (error) => {
      console.error("Ошибка ElevenLabs:", error);
      toast.error(`Ошибка голосового ассистента: ${error.message || "Неизвестная ошибка"}`);
      setIsListening(false);
      setIsSpeaking(false);
    },
    clientTools: {
      // Возвращаем функцию создания задачи
      createTask: async (params) => {
        if (!createTask) {
          console.error("createTask function is not available");
          return "Error: Task creation function is not available";
        }
        
        try {
          await createTask({
            title: params.title,
            description: params.description,
            priority: params.priority,
            houseNumber: params.houseNumber
          });
          return "Задача успешно создана";
        } catch (error) {
          console.error("Error creating task:", error);
          return `Ошибка при создании задачи: ${error.message}`;
        }
      },

      // Tool to get bookings
      getBookings: async (params) => {
        try {
          console.log("Вызван метод getBookings с параметрами:", params);
          
          let query = supabase
            .from('bookings')
            .select('*');
          
          // Filter by guest name if provided
          if (params.guestName) {
            query = query.ilike('guest_name', `%${params.guestName}%`);
          }
          
          // Filter by status if provided
          if (params.status) {
            query = query.eq('status', params.status);
          }
          
          // Filter by start date if provided
          if (params.startDate) {
            query = query.gte('start_date', params.startDate);
          }
          
          // Filter by end date if provided
          if (params.endDate) {
            query = query.lte('end_date', params.endDate);
          }
          
          // Apply order and limit
          query = query.order('created_at', { ascending: false })
                      .limit(params.limit || 10);
          
          const { data, error } = await query;
          
          if (error) {
            console.error("Ошибка при получении данных бронирований:", error);
            throw error;
          }
          
          console.log(`Найдено ${data?.length || 0} бронирований`);
          
          return {
            success: true,
            data: data || [],
            message: `Найдено ${data?.length || 0} бронирований`
          };
        } catch (error) {
          console.error('Ошибка при получении бронирований:', error);
          return {
            success: false,
            data: [],
            message: `Ошибка при получении бронирований: ${error.message || 'Неизвестная ошибка'}`
          };
        }
      },
      
      // Tool to get WhatsApp messages
      getWhatsAppMessages: async (params) => {
        try {
          console.log("Вызван метод getWhatsAppMessages с параметрами:", params);
          
          let query = supabase
            .from('whatsapp_messages')
            .select('*');
          
          // Filter by phone if provided (с учетом возможных форматов)
          if (params.phone) {
            // Очищаем номер от всех символов кроме цифр для сравнения
            const cleanedPhone = params.phone.replace(/\D/g, '');
            if (cleanedPhone.length > 0) {
              query = query.ilike('phone_number', `%${cleanedPhone}%`);
            }
          }
          
          // Filter by guest name if provided
          if (params.guestName) {
            query = query.ilike('guest_name', `%${params.guestName}%`);
          }
          
          // Filter by unread if requested
          if (params.showUnread) {
            query = query.eq('is_read', false);
          }
          
          // Filter by date range if provided
          if (params.startDate) {
            query = query.gte('created_at', params.startDate);
          }
          
          if (params.endDate) {
            query = query.lte('created_at', params.endDate);
          }
          
          // Apply order and limit
          query = query.order('created_at', { ascending: false })
                      .limit(params.limit || 10);
          
          const { data, error } = await query;
          
          if (error) {
            console.error("Ошибка при получении сообщений WhatsApp:", error);
            throw error;
          }
          
          console.log(`Найдено ${data?.length || 0} сообщений WhatsApp`);
          
          return {
            success: true,
            data: data || [],
            message: `Найдено ${data?.length || 0} сообщений WhatsApp`
          };
        } catch (error) {
          console.error('Ошибка при получении сообщений WhatsApp:', error);
          return {
            success: false,
            data: [],
            message: `Ошибка при получении сообщений WhatsApp: ${error.message || 'Неизвестная ошибка'}`
          };
        }
      },
      
      // Tool to get tasks
      getTasks: async (params) => {
        try {
          console.log("Вызван метод getTasks с параметрами:", params);
          
          let query = supabase
            .from('tasks')
            .select(`
              *,
              employees (
                name,
                position
              )
            `);
          
          // Filter by status if provided
          if (params.status) {
            query = query.eq('status', params.status);
          }
          
          // Filter by priority if provided
          if (params.priority) {
            query = query.eq('priority', params.priority);
          }
          
          // Filter by house number if provided
          if (params.houseNumber) {
            query = query.eq('house_number', params.houseNumber);
          }
          
          // Filter by date ranges
          if (params.startDate) {
            query = query.gte('created_at', params.startDate);
          }
          
          if (params.endDate) {
            query = query.lte('created_at', params.endDate);
          }
          
          // Apply order and limit
          query = query.order('created_at', { ascending: false })
                      .limit(params.limit || 10);
          
          const { data, error } = await query;
          
          if (error) {
            console.error("Ошибка при получении задач:", error);
            throw error;
          }
          
          let filteredData = data || [];
          
          // Дополнительная фильтрация по имени сотрудника, если указано
          if (params.employeeName && filteredData.length > 0) {
            filteredData = filteredData.filter(task => 
              task.employees?.name.toLowerCase().includes(params.employeeName?.toLowerCase() || '')
            );
          }
          
          console.log(`Найдено ${filteredData.length} задач`);
          
          return {
            success: true,
            data: filteredData,
            message: `Найдено ${filteredData.length} задач`
          };
        } catch (error) {
          console.error('Ошибка при получении задач:', error);
          return {
            success: false,
            data: [],
            message: `Ошибка при получении задач: ${error.message || 'Неизвестная ошибка'}`
          };
        }
      },
      
      // Tool to cancel booking
      cancelBooking: async (params) => {
        try {
          console.log("Вызван метод cancelBooking с параметрами:", params);
          
          if (!params.id && !params.guestName) {
            throw new Error('Необходимо указать ID бронирования или имя гостя');
          }
          
          // First, find the booking by guest name or ID
          let query = supabase.from('bookings').select('*');
          
          if (params.id) {
            query = query.eq('id', params.id);
          } else if (params.guestName) {
            query = query.ilike('guest_name', `%${params.guestName}%`);
          }
          
          const { data: bookingData, error: findError } = await query.order('created_at', { ascending: false }).limit(1);
          
          if (findError) {
            console.error("Ошибка при поиске бронирования:", findError);
            throw findError;
          }
          
          if (!bookingData || bookingData.length === 0) {
            console.log("Бронирование не найдено");
            return {
              success: false,
              message: 'Бронирование не найдено'
            };
          }
          
          console.log("Найдено бронирование для отмены:", bookingData[0]);
          
          // Now cancel the booking
          const { data, error } = await supabase
            .from('bookings')
            .update({ status: 'cancelled' })
            .eq('id', bookingData[0].id)
            .select();
          
          if (error) {
            console.error("Ошибка при отмене бронирования:", error);
            throw error;
          }
          
          console.log("Бронирование успешно отменено:", data);
          
          return {
            success: true,
            data: bookingData[0],
            message: `Бронирование для ${bookingData[0].guest_name} успешно отменено`
          };
        } catch (error) {
          console.error('Ошибка при отмене бронирования:', error);
          return {
            success: false,
            message: `Ошибка при отмене бронирования: ${error.message || 'Неизвестная ошибка'}`
          };
        }
      },
      
      // Функция для создания бронирования
      createBooking: async (params) => {
        try {
          console.log("Вызван метод createBooking с параметрами:", params);
          
          // Валидация обязательных полей
          if (!params.room_number || !params.guest_name || !params.guest_phone || !params.start_date || !params.end_date) {
            const errorMsg = 'Для создания бронирования необходимы: номер комнаты, имя гостя, телефон, даты заезда и выезда';
            console.error(errorMsg);
            toast.error(errorMsg);
            return {
              success: false,
              message: errorMsg
            };
          }
          
          // Форматируем номер телефона
          const formattedPhone = formatPhoneNumber(params.guest_phone);
          
          // Проверяем доступность номера на указанные даты
          const availabilityCheck = await fetch('https://fwchkusgryebslnjkoly.supabase.co/functions/v1/booking-manager', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              action: 'check-availability',
              booking: {
                room_number: params.room_number,
                start_date: params.start_date,
                end_date: params.end_date
              }
            })
          });
          
          if (!availabilityCheck.ok) {
            const errorText = await availabilityCheck.text();
            toast.error(`Ошибка при проверке доступности: ${availabilityCheck.status}`);
            throw new Error(`Ошибка при проверке доступности: ${errorText}`);
          }
          
          const availabilityResult = await availabilityCheck.json();
          
          if (!availabilityResult.available) {
            const errorMsg = `Номер ${params.room_number} недоступен на указанные даты`;
            toast.error(errorMsg);
            return {
              success: false,
              message: errorMsg
            };
          }
          
          // Создаем бронирование через Edge Function
          const response = await fetch('https://fwchkusgryebslnjkoly.supabase.co/functions/v1/booking-manager', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({
              action: 'create',
              booking: {
                room_number: params.room_number,
                start_date: params.start_date,
                end_date: params.end_date,
                guest_name: params.guest_name,
                guest_phone: formattedPhone,
                price: params.price || null,
                prepayment: params.prepayment || 0,
                comment: params.comment || '',
                source: 'elevenlabs'
              }
            })
          });
          
          if (!response.ok) {
            const errorText = await response.text();
            toast.error(`Ошибка при создании бронирования: ${response.status}`);
            throw new Error(`Ошибка API бронирования: ${response.status} - ${errorText}`);
          }
          
          const result = await response.json();
          console.log("Результат создания бронирования:", result);
          
          if (result.success) {
            toast.success(`Бронирование для ${params.guest_name} успешно создано!`);
            
            // Опционально: отправить уведомление гостю
            try {
              const message = `Здравствуйте, ${params.guest_name}! Ваше бронирование номера ${params.room_number} на даты ${params.start_date} - ${params.end_date} успешно создано. Спасибо, что выбрали наш отель!`;
              
              // Исправлено: передаем строку номера телефона как первый аргумент
              await sendWhatsAppMessage(formattedPhone, message);
            } catch (notifyError) {
              console.error("Ошибка при отправке уведомления:", notifyError);
              // Продолжаем выполнение, так как бронирование уже создано
            }
          }
          
          return {
            success: true,
            booking: result.booking,
            message: `Бронирование для ${params.guest_name} успешно создано!`
          };
        } catch (error) {
          console.error('Ошибка при создании бронирования:', error);
          toast.error(`Ошибка при создании бронирования: ${error.message || 'Неизвестная ошибка'}`);
          return {
            success: false,
            message: `Ошибка при создании бронирования: ${error.message || 'Неизвестная ошибка'}`
          };
        }
      },
      
      // Tool to send WhatsApp message
      sendWhatsAppMessage: async (params) => {
        try {
          console.log("Вызван метод sendWhatsAppMessage с параметрами:", params);
          
          if (!params.phone) {
            const errorMsg = 'Номер телефона обязателен для заполнения';
            console.error(errorMsg);
            toast.error(errorMsg);
            return {
              success: false,
              message: errorMsg
            };
          }
          
          // Форматируем номер телефона - только цифры
          const formattedPhone = formatPhoneNumber(params.phone);
          console.log("Номер после форматирования:", formattedPhone);
          
          // Проверяем длину номера
          if (formattedPhone.length < 10) {
            const errorMsg = 'Некорректный формат номера телефона';
            console.error(errorMsg, { original: params.phone, cleaned: formattedPhone });
            toast.error(errorMsg);
            return {
              success: false,
              message: errorMsg,
              details: { original: params.phone, cleaned: formattedPhone }
            };
          }
          
          // Если сообщение не указано, генерируем его автоматически
          let messageText = params.message;
          if (!messageText) {
            console.log("Сообщение не указано, генерируем автоматически");
            messageText = await generateMessageText(formattedPhone, params.context);
            console.log("Сгенерированное сообщение:", messageText);
          }
          
          // Исправлено: передаем строку номера телефона как первый аргумент и сообщение как второй аргумент
          const result = await sendWhatsAppMessage(formattedPhone, messageText);
          
          return result;
        } catch (error) {
          console.error('Ошибка при отправке WhatsApp сообщения:', error);
          toast.error(`Ошибка при отправке сообщения: ${error.message || 'Неизвестная ошибка'}`);
          return {
            success: false,
            message: `Ошибка при отправке сообщения: ${error.message || 'Неизвестная ошибка'}`
          };
        }
      }
    }
  });

  // Monitor ElevenLabs isSpeaking state
  useEffect(() => {
    setIsSpeaking(conversation.isSpeaking);
  }, [conversation.isSpeaking]);
  
  // Fake volume effect for ElevenLabs speaking animation
  useEffect(() => {
    if (conversation.isSpeaking) {
      const volumeInterval = setInterval(() => {
        // Generate random volume between 3 and 10 for visual feedback when speaking
        setVoiceVolume(Math.floor(Math.random() * 7) + 3);
      }, 150);
      
      return () => clearInterval(volumeInterval);
    } else {
      setVoiceVolume(0);
    }
  }, [conversation.isSpeaking]);

  // Function to toggle ElevenLabs conversation
  const toggleElevenLabsConversation = async () => {
    try {
      if (conversation.status === 'connected') {
        console.log('Ending ElevenLabs conversation');
        await conversation.endSession();
        setIsListening(false);
        setIsSpeaking(false);
        toast.info("Голосовой ассистент деактивирован");
      } else {
        console.log('Starting ElevenLabs conversation');
        const agentId = process.env.NEXT_PUBLIC_AGENT_ID || "bk6zd2EX7Rc7o9LnxEtY"; 
        console.log("Используется Agent ID:", agentId);
        await conversation.startSession({ agentId });
        toast.success("Голосовой ассистент активирован");
      }
    } catch (error) {
      console.error('Error toggling ElevenLabs conversation:', error);
      toast.error(`Ошибка активации ассистента: ${error.message || "Неизвестная ошибка"}`);
    }
  };

  // Загрузка истории чата при открытии диалога
  useEffect(() => {
    if (isDialogOpen) {
      loadChatHistory();
    }
  }, [isDialogOpen, loadChatHistory]);

  // Если виджет встроен, автоматически открываем диалог при инициализации
  useEffect(() => {
    if (embeddedMode) {
      // Задержка для гарантии загрузки компонентов
      const timer = setTimeout(() => {
        setIsDialogOpen(true);
        loadChatHistory();
      }, 500);
      
      return () => clearTimeout(timer);
    }
  }, [embeddedMode, loadChatHistory]);

  // Обработка изменения состояния диалога
  const handleDialogChange = (open: boolean) => {
    setIsDialogOpen(open);
    if (!open) {
      setIsExpanded(false);
      // Останавливаем ElevenLabs при закрытии
      if (conversation.status === 'connected') {
        conversation.endSession();
      }
    } else if (open && embeddedMode) {
      // В embed режиме активируем разговор автоматически
      setTimeout(() => {
        if (!isListening && conversation.status !== 'connected') {
          toggleElevenLabsConversation();
        }
      }, 1000);
    }
  };
  
  // Переключение полноэкранного режима
  const toggleFullscreen = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <>
      <Dialog open={isDialogOpen} onOpenChange={handleDialogChange}>
        {!embeddedMode ? (
          <DialogTrigger asChild>
            <VoiceDialogTrigger ref={triggerRef} />
          </DialogTrigger>
        ) : null}
        
        <VoiceDialogContainer
          isExpanded={isExpanded}
          toggleFullscreen={toggleFullscreen}
          dialogPosition={dialogPosition}
          isListening={isListening}
          isSpeaking={isSpeaking}
          voiceVolume={voiceVolume}
          transcript={transcript}
          onCircleClick={toggleElevenLabsConversation}
          userInput={userInput}
          onUserInputChange={(e) => setUserInput(e.target.value)}
          onSendMessage={handleSendMessage}
          messages={messages}
          isLoading={isLoading}
          createTask={createTask}
        />
      </Dialog>
      
      {/* Hidden button для embeddedMode, чтобы триггер работал */}
      {embeddedMode && (
        <div style={{ position: 'fixed', bottom: '-100px', right: '-100px', visibility: 'hidden' }}>
          <button ref={triggerRef}>Hidden Trigger</button>
        </div>
      )}
    </>
  );
};

export default VoiceAssistantWidget;
