
import React from 'react';
import { cn } from '@/lib/utils';
import { marked } from 'marked';

interface MessageBubbleProps {
  content: string;
  isUser: boolean;
}

const MessageBubble: React.FC<MessageBubbleProps> = ({ content, isUser }) => {
  // Convert markdown to HTML for better formatting of messages
  const renderFormattedContent = () => {
    if (!content) return { __html: '' };
    
    try {
      // Use marked to parse markdown
      const formattedHtml = marked(content);
      return { __html: formattedHtml };
    } catch (error) {
      console.error('Error parsing markdown:', error);
      return { __html: content };
    }
  };
  
  return (
    <div className={cn(
      "flex mb-2", 
      isUser ? "justify-end" : "justify-start"
    )}>
      <div className={cn(
        "max-w-[80%] p-3 rounded-lg shadow-sm",
        isUser 
          ? "bg-blue-600 text-white rounded-tr-none" 
          : "bg-gray-800/80 backdrop-blur-sm text-gray-100 rounded-tl-none"
      )}>
        {content ? (
          <div 
            className="prose prose-sm prose-invert max-w-none break-words"
            dangerouslySetInnerHTML={renderFormattedContent()} 
          />
        ) : (
          <span className="text-gray-400 italic">Пустое сообщение</span>
        )}
      </div>
    </div>
  );
};

export default MessageBubble;
