
import React from 'react';
import VoiceCircle from './VoiceCircle';
import { ChatMessage } from '@/services/chatService';
import StatusMessage from './StatusMessage';
import MessageList from './MessageList';
import ChatInput from './ChatInput';

interface VoiceDialogContentProps {
  isListening: boolean;
  isSpeaking: boolean;
  voiceVolume: number;
  transcript: string;
  onCircleClick: () => void;
  isExpanded: boolean;
  isInputVisible: boolean;
  userInput: string;
  onUserInputChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onSendMessage: () => void;
  messages: ChatMessage[];
  isLoading: boolean;
  createTask?: (details: {
    title: string,
    description: string,
    priority?: 'high' | 'medium' | 'low',
    houseNumber?: string
  }) => Promise<void>;
}

const VoiceDialogContent: React.FC<VoiceDialogContentProps> = ({
  isListening,
  isSpeaking,
  voiceVolume,
  transcript,
  onCircleClick,
  isExpanded,
  isInputVisible,
  userInput,
  onUserInputChange,
  onSendMessage,
  messages,
  isLoading,
  createTask
}) => {
  return (
    <div className={`flex flex-col items-center justify-between ${isExpanded ? 'h-[80vh]' : 'h-[500px]'} transition-all duration-300`}>
      {/* Status message component */}
      <StatusMessage isListening={isListening} isSpeaking={isSpeaking} />
      
      {/* Message list component */}
      <MessageList 
        messages={messages} 
        isLoading={isLoading} 
        isExpanded={isExpanded} 
      />
      
      {/* Bottom controls area with microphone and input field */}
      <div className="w-full flex items-center justify-center mb-4 transition-all duration-300">
        <div className="flex items-center justify-between w-full px-4 gap-4">
          {/* Voice circle */}
          <div className="transition-all duration-300">
            <VoiceCircle 
              isListening={isListening} 
              isSpeaking={isSpeaking} 
              voiceVolume={voiceVolume} 
              onClick={onCircleClick}
            />
          </div>
          
          {/* Chat input component */}
          <ChatInput
            userInput={userInput}
            onUserInputChange={onUserInputChange}
            onSendMessage={onSendMessage}
            isLoading={isLoading}
          />
        </div>
      </div>
    </div>
  );
};

export default VoiceDialogContent;
