
import React from 'react';
import { DialogContent } from "@/components/ui/dialog";
import { Maximize, Minimize } from 'lucide-react';
import VoiceDialogContent from './VoiceDialogContent';
import { ChatMessage } from '@/services/chatService';

interface VoiceDialogContainerProps {
  isExpanded: boolean;
  toggleFullscreen: () => void;
  dialogPosition: { top: number; left: number };
  isListening: boolean;
  isSpeaking: boolean;
  voiceVolume: number;
  transcript: string;
  onCircleClick: () => void;
  userInput: string;
  onUserInputChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onSendMessage: () => void;
  messages: ChatMessage[];
  isLoading: boolean;
  createTask?: (details: {
    title: string,
    description: string,
    priority?: 'high' | 'medium' | 'low',
    houseNumber?: string
  }) => Promise<void>;
}

const VoiceDialogContainer: React.FC<VoiceDialogContainerProps> = ({
  isExpanded,
  toggleFullscreen,
  dialogPosition,
  isListening,
  isSpeaking,
  voiceVolume,
  transcript,
  onCircleClick,
  userInput,
  onUserInputChange,
  onSendMessage,
  messages,
  isLoading,
  createTask
}) => {
  // Determine if we're in an iframe (embedded)
  const isEmbedded = React.useMemo(() => {
    try {
      return window.self !== window.top;
    } catch (e) {
      return true;
    }
  }, []);

  return (
    <DialogContent 
      className={`sm:max-w-md bg-gradient-to-b from-gray-900 to-gray-800 border-none shadow-2xl animate-fade-in ${isExpanded ? 'fixed inset-0 max-w-full max-h-full rounded-none' : 'rounded-xl'}`}
      style={!isExpanded ? {
        position: 'absolute',
        top: `${Math.max(20, dialogPosition.top)}px`, // Keep minimum 20px from top
        left: `${dialogPosition.left + 3}px`, // Keep 3px right shift
        transform: 'none',
        minHeight: '500px', // Keep the height from previous change
        width: '420px', // Keep the width from previous change
        ...(isEmbedded ? { position: 'fixed', top: '20px', right: '20px', left: 'auto' } : {})
      } : {
        position: 'fixed',
        top: '0',
        left: '0',
        right: '0',
        bottom: '0',
        width: '100%',
        height: '100%',
        maxWidth: '100%',
        transform: 'none',
        margin: '0',
        borderRadius: '0'
      }}
    >
      {/* Only fullscreen toggle button in top controls */}
      <div className="absolute right-4 top-4 flex items-center gap-2 z-10">
        <button 
          onClick={toggleFullscreen}
          className="text-gray-300 hover:text-white transition-colors p-2 bg-gray-800/50 rounded-full"
        >
          {isExpanded ? <Minimize size={18} /> : <Maximize size={18} />}
        </button>
      </div>
      
      <VoiceDialogContent
        isListening={isListening}
        isSpeaking={isSpeaking}
        voiceVolume={voiceVolume}
        transcript={transcript}
        onCircleClick={onCircleClick}
        isExpanded={isExpanded}
        isInputVisible={true}
        userInput={userInput}
        onUserInputChange={onUserInputChange}
        onSendMessage={onSendMessage}
        messages={messages}
        isLoading={isLoading}
        createTask={createTask}
      />
    </DialogContent>
  );
};

export default VoiceDialogContainer;
