import { useState, useEffect, useRef } from 'react';
import { sendMessage, getMessageHistory, ChatMessage } from '@/services/chatService';
import { marked } from 'marked';
import { supabase } from "@/integrations/supabase/client";

export const useVoiceChat = () => {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [userInput, setUserInput] = useState("");
  const messageEndRef = useRef<HTMLDivElement | null>(null);

  // Load chat history - увеличено количество сообщений для загрузки с 15 до 25
  const loadChatHistory = async () => {
    const history = await getMessageHistory(25);
    setMessages(history);
  };

  // Handle sending a text message
  const handleSendMessage = () => {
    if (userInput.trim()) {
      handleSendMessageText(userInput);
      setUserInput("");
    }
  };

  // Common function for sending messages - optimized for faster responses
  const handleSendMessageText = async (text: string) => {
    if (!text.trim() || isLoading) return;
    
    setIsLoading(true);
    
    // Check for confirmation messages for booking cancellation
    if (isConfirmationMessage(text)) {
      await handlePendingActions(text);
      setIsLoading(false);
      return;
    }
    
    // Add user message to history with loading flag
    const tempMessage: ChatMessage = {
      user_query: text,
      bot_response: "",
      isLoading: true
    };
    
    setMessages(prev => [...prev, tempMessage]);
    
    try {
      // Send message and get response
      const response = await sendMessage(text);
      
      if (response) {
        // Immediately update message history with received response
        setMessages(prev => prev.filter(msg => !msg.isLoading).concat([response]));
        
        // Check if the response contains a booking cancellation or WhatsApp-related request
        await checkAndHandleSpecialRequests(response.bot_response, text);
      } else {
        // If no response received, remove loading flag but keep user message
        setMessages(prev => prev.map(msg => 
          msg.isLoading ? { ...msg, isLoading: false, bot_response: "Не удалось получить ответ" } : msg
        ));
      }
    } catch (error) {
      console.error("Ошибка при обработке сообщения:", error);
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { ...msg, isLoading: false, bot_response: "Произошла ошибка" } : msg
      ));
    } finally {
      setIsLoading(false);
    }
  };
  
  // Функция для проверки подтверждающих сообщений
  const isConfirmationMessage = (text: string): boolean => {
    const confirmationKeywords = [
      'подтверждаю', 'да', 'точно', 'согласен', 'подтвердить', 
      'отменить', 'отмени', 'удали', 'удалить', 'убери'
    ];
    
    return confirmationKeywords.some(keyword => 
      text.toLowerCase().includes(keyword)
    );
  };
  
  // Функция для обработки ожидающих подтверждения действий
  const handlePendingActions = async (text: string) => {
    // Проверяем последние 3 сообщения на наличие запроса подтверждения отмены бронирования
    const recentMessages = messages.slice(-3);
    const cancellationRequest = recentMessages.find(msg => 
      msg.bot_response && (
        msg.bot_response.toLowerCase().includes('вы действительно хотите отменить бронирование') ||
        msg.bot_response.toLowerCase().includes('подтвердите отмену бронирования')
      )
    );
    
    if (cancellationRequest) {
      // Извлекаем имя гостя из запроса
      const botResponse = cancellationRequest.bot_response;
      const guestNameMatch = botResponse.match(/бронирование\s+(\w+\s+\w+|\w+)/i);
      const guestName = guestNameMatch ? guestNameMatch[1] : null;
      
      if (guestName && isConfirmMessage(text)) {
        // Добавляем сообщение о подтверждении
        const confirmMsg: ChatMessage = {
          user_query: text,
          bot_response: "",
          isLoading: true
        };
        
        setMessages(prev => [...prev, confirmMsg]);
        
        try {
          // Получаем данные о бронировании
          const { data: bookingData, error } = await supabase
            .from('bookings')
            .select('*')
            .ilike('guest_name', `%${guestName}%`)
            .eq('status', 'confirmed')
            .limit(1)
            .single();
            
          if (error) {
            throw new Error(`Ошибка при получении данных бронирования: ${error.message}`);
          }
          
          if (bookingData) {
            // Отменяем бронирование
            await manageBooking('delete', { id: bookingData.id, guest_name: bookingData.guest_name });
            
            // Обновляем сообщение с результатом
            setMessages(prev => prev.map(msg => 
              msg.isLoading ? { 
                ...msg, 
                isLoading: false, 
                bot_response: `✅ Бронирование для ${bookingData.guest_name} успешно отменено!` 
              } : msg
            ));
          } else {
            // Если бронирование не найдено
            setMessages(prev => prev.map(msg => 
              msg.isLoading ? { 
                ...msg, 
                isLoading: false, 
                bot_response: `❌ Не удалось найти активное бронирование для ${guestName}` 
              } : msg
            ));
          }
        } catch (error) {
          console.error("Ошибка при отмене бронирования:", error);
          // Обновляем сообщение с ошибкой
          setMessages(prev => prev.map(msg => 
            msg.isLoading ? { 
              ...msg, 
              isLoading: false, 
              bot_response: `❌ Не удалось отменить бронирование: ${error.message}` 
            } : msg
          ));
        }
        return true;
      }
    }
    
    return false;
  };
  
  // Проверка на подтверждающее сообщение
  const isConfirmMessage = (text: string): boolean => {
    const confirmWords = ['подтверждаю', 'да', 'точно', 'согласен', 'подтвердить'];
    return confirmWords.some(word => text.toLowerCase().includes(word));
  };
  
  // Функция для проверки и обработки специальных запросов
  const checkAndHandleSpecialRequests = async (botResponse: string, userQuery: string) => {
    // Проверка на запрос отмены бронирования (обработка происходит в GPT)
    const isCancellationRequest = userQuery.toLowerCase().includes('отмени бронь') || 
                                 userQuery.toLowerCase().includes('отменить бронь') ||
                                 userQuery.toLowerCase().includes('удали бронирование');
    
    // Проверка на запрос показа WhatsApp сообщений
    const isWhatsAppViewRequest = userQuery.toLowerCase().includes('покажи сообщения') || 
                                userQuery.toLowerCase().includes('покажи сообщение от') ||
                                userQuery.toLowerCase().includes('покажи непрочитанные') ||
                                userQuery.toLowerCase().includes('покажи переписку');
    
    if (isWhatsAppViewRequest && botResponse.includes('сообщение') && botResponse.includes('найдено')) {
      // Уже обработано в GPT, дополнительных действий не требуется
      console.log("Запрос на просмотр сообщений WhatsApp обработан в GPT");
    }
    
    // Проверка на подтверждение отмены бронирования
    if (isCancellationRequest && botResponse.includes('действительно')) {
      console.log("Обнаружен запрос на подтверждение отмены бронирования");
      // Обработка будет выполнена при следующем сообщении пользователя
    }
  };
  
  // Функция для получения сообщений WhatsApp
  const getWhatsAppMessages = async (params: {
    guestName?: string,
    phoneNumber?: string,
    onlyUnread?: boolean,
    limit?: number
  }) => {
    try {
      setIsLoading(true);
      
      // Формируем запрос в зависимости от параметров
      let query = supabase.from('whatsapp_messages').select('*');
      
      // Фильтруем по имени гостя, если указано
      if (params.guestName) {
        query = query.ilike('guest_name', `%${params.guestName}%`);
      }
      
      // Фильтруем по номеру телефона, если указан
      if (params.phoneNumber) {
        query = query.eq('phone_number', params.phoneNumber);
      }
      
      // Фильтруем по непрочитанным, если указано
      if (params.onlyUnread) {
        query = query.eq('is_read', false);
      }
      
      // Сортируем по дате создания (сначала новые)
      query = query.order('created_at', { ascending: false });
      
      // Ограничиваем количество результатов
      query = query.limit(params.limit || 20);
      
      // Выполняем запрос
      const { data: messages, error } = await query;
      
      if (error) {
        throw new Error(`Ошибка при получении сообщений WhatsApp: ${error.message}`);
      }
      
      // Если найдены сообщения, добавляем их в чат
      if (messages && messages.length > 0) {
        // Формируем красивый ответ с форматированием для каждого сообщения
        const formattedResponse = formatWhatsAppMessages(messages, params);
        
        // Добавляем в историю чата
        const responseMessage: ChatMessage = {
          user_query: `Показать сообщения${params.guestName ? ` от ${params.guestName}` : ''}${params.onlyUnread ? ' (непрочитанные)' : ''}`,
          bot_response: formattedResponse,
          isLoading: false
        };
        
        setMessages(prev => [...prev, responseMessage]);
        
        // Если это были непрочитанные сообщения, помечаем их как прочитанные
        if (params.onlyUnread) {
          const messageIds = messages.map(msg => msg.id);
          await supabase
            .from('whatsapp_messages')
            .update({ is_read: true })
            .in('id', messageIds);
        }
        
        return messages;
      } else {
        // Если сообщений не найдено
        const noMessagesResponse: ChatMessage = {
          user_query: `Показать сообщения${params.guestName ? ` от ${params.guestName}` : ''}${params.onlyUnread ? ' (непрочитанные)' : ''}`,
          bot_response: `📱 Сообщений${params.guestName ? ` от ${params.guestName}` : ''}${params.onlyUnread ? ' (непрочитанных)' : ''} не найдено.`,
          isLoading: false
        };
        
        setMessages(prev => [...prev, noMessagesResponse]);
        return [];
      }
    } catch (error) {
      console.error("Ошибка при получении сообщений WhatsApp:", error);
      
      // Добавляем сообщение об ошибке
      const errorMessage: ChatMessage = {
        user_query: `Показать сообщения${params.guestName ? ` от ${params.guestName}` : ''}`,
        bot_response: `❌ Не удалось получить сообщения: ${error.message}`,
        isLoading: false
      };
      
      setMessages(prev => [...prev, errorMessage]);
      return [];
    } finally {
      setIsLoading(false);
    }
  };
  
  // Функция для форматирования сообщений WhatsApp
  const formatWhatsAppMessages = (messages: any[], params: any): string => {
    if (!messages || messages.length === 0) {
      return "Сообщений не найдено";
    }
    
    let result = "";
    
    // Заголовок с информацией о выборке
    if (params.guestName) {
      result += `📱 **Сообщения ${params.onlyUnread ? 'непрочитанные ' : ''}от ${params.guestName}**\n\n`;
    } else if (params.onlyUnread) {
      result += `📱 **Непрочитанные сообщения**\n\n`;
    } else {
      result += `📱 **Последние сообщения WhatsApp**\n\n`;
    }
    
    // Группируем сообщения по номеру телефона/имени гостя
    const groupedMessages: {[key: string]: any[]} = {};
    
    messages.forEach(msg => {
      const key = msg.guest_name || msg.phone_number;
      if (!groupedMessages[key]) {
        groupedMessages[key] = [];
      }
      groupedMessages[key].push(msg);
    });
    
    // Для каждой группы выводим сообщения в хронологическом порядке
    Object.keys(groupedMessages).forEach(key => {
      if (Object.keys(groupedMessages).length > 1) {
        result += `### ${key}\n\n`;
      }
      
      // Сортируем сообщения в хронологическом порядке
      const sortedMessages = groupedMessages[key].sort((a, b) => 
        new Date(a.created_at).getTime() - new Date(b.created_at).getTime()
      );
      
      sortedMessages.forEach(msg => {
        const date = new Date(msg.created_at);
        const formattedDate = `${date.toLocaleDateString('ru-RU')} ${date.toLocaleTimeString('ru-RU')}`;
        const direction = msg.is_incoming ? "📥" : "📤";
        const status = !msg.is_read && msg.is_incoming ? " (Непрочитано)" : "";
        
        result += `${direction} **${formattedDate}**${status}:\n${msg.content}\n\n`;
      });
    });
    
    return result;
  };

  // Функция для проверки и обработки запросов на отправку WhatsApp сообщений
  const checkAndHandleWhatsAppRequest = (botResponse: string, userQuery: string) => {
    const whatsappKeywords = ['отправить сообщение', 'отправить whatsapp', 'отправь whatsapp', 
                             'напиши whatsapp', 'отправь сообщение', 'выключить музыку'];
    
    // Проверяем, есть ли в запросе пользователя ключев��е слова для WhatsApp
    const isWhatsAppRequest = whatsappKeywords.some(keyword => userQuery.toLowerCase().includes(keyword));
    
    // Если это запрос на WhatsApp и есть информация о номере телефона - подготавливаем данные для отправки
    if (isWhatsAppRequest && botResponse.includes('телефон') && botResponse.includes('сообщение')) {
      console.log("Обнаружен запрос на отправку WhatsApp:", userQuery);
      // Функционал будет использоваться через подтверждение пользователя в интерфейсе
    }
  };

  // Create a task from the assistant - улучшенная версия с расширенным логированием
  const createTask = async (details: {
    title: string,
    description: string,
    priority?: 'high' | 'medium' | 'low',
    houseNumber?: string
  }) => {
    try {
      console.log("Начало создания задачи:", details);
      setIsLoading(true);
      
      // Add temporary message about task creation
      const tempMessage: ChatMessage = {
        user_query: `Создай задачу: ${details.title}`,
        bot_response: "Создаю задачу...",
        isLoading: true
      };
      
      setMessages(prev => [...prev, tempMessage]);
      
      // Get the current session token for authentication
      const { data: { session } } = await supabase.auth.getSession();
      const authToken = session?.access_token;
      
      console.log("Подготовка запроса к Edge Function task-manager");
      
      // Send task creation request to the edge function with auth token if available
      const headers: HeadersInit = {
        'Content-Type': 'application/json'
      };
      
      if (authToken) {
        console.log("Добавляем токен авторизации в запрос");
        headers['Authorization'] = `Bearer ${authToken}`;
      } else {
        console.log("Токен авторизации не найден, выполняем запрос анонимно");
      }
      
      const response = await fetch('https://fwchkusgryebslnjkoly.supabase.co/functions/v1/task-manager', {
        method: 'POST',
        headers,
        body: JSON.stringify({
          action: 'create',
          task: {
            title: details.title,
            description: details.description,
            priority: details.priority || 'medium',
            house_number: details.houseNumber || '1'
          }
        })
      });
      
      console.log("Получен ответ от Edge Function, статус:", response.status);
      
      if (!response.ok) {
        const errorText = await response.text();
        console.error(`Ошибка при создании задачи: ${response.status}`, errorText);
        throw new Error(`Ошибка при создании задачи: ${response.status} - ${errorText}`);
      }
      
      const result = await response.json();
      console.log("Результат создания задачи:", result);
      
      // Update message with success response
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `✅ Задача "${details.title}" успешно создана!${result.task?.id ? ` ID: ${result.task.id}` : ''}${result.employee ? `, назначена на: ${result.employee}` : ''}` 
        } : msg
      ));
      
    } catch (error) {
      console.error("Ошибка при создании задачи:", error);
      
      // Update message with error information
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `❌ Не удалось создать задачу: ${error.message}` 
        } : msg
      ));
    } finally {
      setIsLoading(false);
    }
  };

  // Send WhatsApp message
  const sendWhatsAppMessage = async (phoneNumber: string, message: string) => {
    try {
      setIsLoading(true);
      
      // Add temporary message about WhatsApp sending
      const tempMessage: ChatMessage = {
        user_query: `Отправь WhatsApp на ${phoneNumber}: ${message}`,
        bot_response: "Отправляю сообщение...",
        isLoading: true
      };
      
      setMessages(prev => [...prev, tempMessage]);
      
      // Send WhatsApp message request to the edge function
      const response = await fetch('https://fwchkusgryebslnjkoly.supabase.co/functions/v1/whatsapp-sender', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          phone: phoneNumber,
          message: message
        })
      });
      
      if (!response.ok) {
        throw new Error(`Ошибка при отправке WhatsApp: ${response.status}`);
      }
      
      const result = await response.json();
      
      // Update message with success response
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `✅ Сообщение WhatsApp успешно отправлено на номер ${phoneNumber}` 
        } : msg
      ));
      
    } catch (error) {
      console.error("Ошибка при отправке WhatsApp:", error);
      
      // Update message with error information
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `❌ Не удалось отправить WhatsApp: ${error.message}` 
        } : msg
      ));
    } finally {
      setIsLoading(false);
    }
  };

  // Manage bookings
  const manageBooking = async (action: 'create' | 'update' | 'delete', bookingData: any) => {
    try {
      setIsLoading(true);
      
      let actionText = action === 'create' ? 'Создаю' : action === 'update' ? 'Обновляю' : 'Удаляю';
      
      // Add temporary message about booking management
      const tempMessage: ChatMessage = {
        user_query: `${actionText} бронирование: ${bookingData.guest_name || 'Гость'}, даты: ${bookingData.start_date || 'не указано'} - ${bookingData.end_date || 'не указано'}`,
        bot_response: `${actionText} бронирование...`,
        isLoading: true
      };
      
      setMessages(prev => [...prev, tempMessage]);
      
      // Send booking management request to the edge function
      const response = await fetch('https://fwchkusgryebslnjkoly.supabase.co/functions/v1/booking-manager', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          action,
          booking: bookingData,
          bookingId: bookingData.id // Добавляем для случаев update и delete
        })
      });
      
      if (!response.ok) {
        throw new Error(`Ошибка при работе с бронированием: ${response.status}`);
      }
      
      const result = await response.json();
      
      // Map actions to success messages
      const successMessages = {
        'create': `✅ Бронирование для ${bookingData.guest_name} успешно создано!`,
        'update': `✅ Бронирование для ${bookingData.guest_name} успешно обновлено!`,
        'delete': `✅ Бронирование для ${bookingData.guest_name} успешно отменено!`
      };
      
      // Update message with success response
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: successMessages[action] 
        } : msg
      ));
      
    } catch (error) {
      console.error("Ошибка при работе с бронированием:", error);
      
      // Update message with error information
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `❌ Не удалось выполнить операцию с бронированием: ${error.message}` 
        } : msg
      ));
    } finally {
      setIsLoading(false);
    }
  };

  return {
    messages,
    isLoading,
    userInput,
    setUserInput,
    loadChatHistory,
    handleSendMessage,
    handleSendMessageText,
    createTask,
    sendWhatsAppMessage,
    manageBooking,
    getWhatsAppMessages,
  };
};
