
import React, { forwardRef } from 'react';

interface VoiceDialogTriggerProps {
  onClick?: () => void;
}

const VoiceDialogTrigger = forwardRef<HTMLButtonElement, VoiceDialogTriggerProps>((props, ref) => {
  return (
    <button 
      ref={ref}
      className="fixed bottom-6 right-6 h-12 w-12 rounded-full bg-gradient-to-t from-blue-500 to-blue-300 shadow-lg flex items-center justify-center z-50 transition-transform hover:scale-105"
      onClick={props.onClick}
    >
      <div className="h-4 w-4 bg-white opacity-40 rounded-full"></div>
    </button>
  );
});

VoiceDialogTrigger.displayName = 'VoiceDialogTrigger';

export default VoiceDialogTrigger;
