
import { supabase } from "@/integrations/supabase/client";
import { toast } from "@/components/ui/sonner";

// Интерфейс для сообщений чата
export interface ChatMessage {
  id?: string;
  timestamp?: string;
  user_query: string;
  bot_response: string;
  isLoading?: boolean;
}

// Функция для отправки сообщения в базу данных
export const sendMessage = async (userMessage: string): Promise<ChatMessage | null> => {
  try {
    console.log("Отправка сообщения:", userMessage);

    // Отправка запроса в таблицу conversations
    const { data, error } = await supabase
      .from('conversations')
      .insert([{ user_query: userMessage, bot_response: "" }])
      .select()
      .single();

    if (error) {
      console.error("Ошибка при отправке сообщения:", error);
      toast.error("Ошибка при отправке сообщения");
      return null;
    }

    let response;
    
    try {
      // Вызов yandex-gpt-proxy функции для получения ответа от Яндекс GPT
      response = await Promise.race([
        supabase.functions.invoke('yandex-gpt-proxy', {
          body: {
            message: userMessage,
            conversationId: data.id
          }
        }),
        // 15-секундный таймаут для вызова функции (увеличен с 10 до 15, т.к. теперь обрабатываем больше данных)
        new Promise((_, reject) => 
          setTimeout(() => reject(new Error("Превышено время ожидания от сервера")), 15000)
        )
      ]);
    } catch (err) {
      console.error("Ошибка вызова функции:", err);
      toast.error("Сервер не ответил вовремя, но ваше сообщение сохранено");
      
      // Обновляем запись в базе данных с информацией об ошибке
      await supabase
        .from('conversations')
        .update({ bot_response: "Ответ не получен. Пожалуйста, повторите запрос позже." })
        .eq('id', data.id);
        
      // Возвращаем сообщение с информацией об ошибке
      return {
        ...data,
        bot_response: "Ответ не получен. Пожалуйста, повторите запрос позже."
      };
    }

    if (response.error) {
      console.error("Ошибка вызова функции:", response.error);
      toast.error("Произошла ошибка при обработке запроса");
      
      // Обновляем запись в базе данных с информацией об ошибке webhook
      await supabase
        .from('conversations')
        .update({ bot_response: "Функция обработки сообщений временно недоступна." })
        .eq('id', data.id);
      
      return {
        ...data,
        bot_response: "Функция обработки сообщений временно недоступна."
      };
    }

    // Немедленно получаем обновленный ответ из базы без искусственной задержки
    const { data: updatedData, error: fetchError } = await supabase
      .from('conversations')
      .select()
      .eq('id', data.id)
      .single();

    if (fetchError) {
      console.error("Ошибка при получении ответа:", fetchError);
      toast.error("Не удалось получить ответ");
      return data; // Возвращаем исходные данные
    }

    // Если был получен прямой ответ от функции, используем его для быстрого отображения
    if (response?.data?.bot_response) {
      console.log("Используем прямой ответ из функции:", response.data.bot_response);
      return {
        ...updatedData,
        bot_response: response.data.bot_response
      };
    }

    // Если ответ пустой, сообщаем об этом
    if (!updatedData.bot_response) {
      console.log("Пустой ответ от сервера");
      
      // Обновляем запись в базе данных с информацией о пустом ответе
      await supabase
        .from('conversations')
        .update({ bot_response: "Бот не смог сформулировать ответ. Пожалуйста, попробуйте задать вопрос по-другому." })
        .eq('id', data.id);
      
      return {
        ...updatedData,
        bot_response: "Бот не смог сформулировать ответ. Пожалуйста, попробуйте задать вопрос по-другому."
      };
    }

    console.log("Получен ответ:", updatedData);
    return updatedData;
  } catch (err) {
    console.error("Произошла ошибка:", err);
    toast.error("Произошла ошибка при отправке сообщения");
    return null;
  }
};

// Функция для получения истории сообщений
export const getMessageHistory = async (limit = 25): Promise<ChatMessage[]> => {
  try {
    const { data, error } = await supabase
      .from('conversations')
      .select('*')
      .order('timestamp', { ascending: false })
      .limit(limit);

    if (error) {
      console.error("Ошибка при получении истории сообщений:", error);
      toast.error("Не удалось загрузить историю сообщений");
      return [];
    }

    return data.reverse();
  } catch (err) {
    console.error("Произошла ошибка при загрузке истории:", err);
    toast.error("Произошла ошибка при загрузке истории");
    return [];
  }
};
