
import React from 'react';
import { Loader } from 'lucide-react';

interface ChatInputProps {
  userInput: string;
  onUserInputChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onSendMessage: () => void;
  isLoading: boolean;
}

const ChatInput: React.FC<ChatInputProps> = ({
  userInput,
  onUserInputChange,
  onSendMessage,
  isLoading
}) => {
  // Handle Enter key press
  const handleKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      onSendMessage();
    }
  };

  return (
    <div className="flex-grow flex items-center gap-2 bg-gray-700/30 rounded-full pl-4 pr-2 py-2 animate-fade-in">
      <input
        type="text"
        className="bg-transparent border-none outline-none text-gray-100 flex-grow text-sm"
        placeholder="Напишите сообщение..."
        value={userInput}
        onChange={onUserInputChange}
        onKeyPress={handleKeyPress}
        disabled={isLoading}
      />
      <button 
        onClick={onSendMessage}
        className={`p-2 ${isLoading ? 'bg-gray-500' : 'bg-blue-500 hover:bg-blue-600'} rounded-full transition-colors`}
        disabled={isLoading}
      >
        {isLoading ? (
          <Loader size={16} className="text-white animate-spin" />
        ) : (
          <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-white" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M22 2L11 13" />
            <path d="M22 2L15 22L11 13L2 9L22 2Z" />
          </svg>
        )}
      </button>
    </div>
  );
};

export default ChatInput;
