
import React from 'react';

interface StatusMessageProps {
  isListening: boolean;
  isSpeaking: boolean;
}

const StatusMessage: React.FC<StatusMessageProps> = ({ isListening, isSpeaking }) => {
  // Определяем статусное сообщение
  const getStatusMessage = () => {
    if (isSpeaking) {
      return "Ассистент говорит...";
    }
    
    if (isListening) {
      return "Ассистент слушает...";
    }
    
    return "Нажмите на круг для активации ассистента";
  };

  return (
    <div className="w-full text-center mb-4">
      <h2 className="text-xl font-medium text-gray-100 h-8 flex items-center justify-center transition-all duration-300">
        {getStatusMessage()}
      </h2>
    </div>
  );
};

export default StatusMessage;
