
import { useState, useEffect, useRef, RefObject } from 'react';

interface DialogPositionOptions {
  offsetTop?: number;
  offsetLeft?: number;
  embedded?: boolean;
}

export const useDialogPosition = (options: DialogPositionOptions = {}) => {
  const { offsetTop = -600, offsetLeft = -450, embedded = false } = options;
  const triggerRef = useRef<HTMLButtonElement>(null);
  const [dialogPosition, setDialogPosition] = useState({ top: 0, left: 0 });

  // Calculate dialog position based on trigger button
  const updatePosition = () => {
    if (triggerRef.current) {
      const rect = triggerRef.current.getBoundingClientRect();
      
      // If embedded, use different positioning strategy
      if (embedded) {
        // Position in the center of the screen for embedded mode
        const windowHeight = window.innerHeight;
        const windowWidth = window.innerWidth;
        
        setDialogPosition({
          top: windowHeight / 2 - 300, // Center vertically
          left: windowWidth / 2 - 210, // Center horizontally
        });
      } else {
        // Use the original positioning strategy
        setDialogPosition({
          top: rect.top + offsetTop, 
          left: rect.left + offsetLeft,
        });
      }
    }
  };

  // Update position on window resize and initial render
  useEffect(() => {
    window.addEventListener('resize', updatePosition);
    updatePosition();
    
    return () => window.removeEventListener('resize', updatePosition);
  }, [embedded, offsetTop, offsetLeft]);

  return {
    triggerRef,
    dialogPosition,
    updatePosition
  };
};
