
import React from 'react';
import ReactDOM from 'react-dom/client';
import { createRoot } from 'react-dom/client';
import VoiceAssistantWidget from '../components/VoiceAssistantWidget';
import { supabase } from '../integrations/supabase/client';
import '../index.css';

class VoiceAssistantElement extends HTMLElement {
  private shadow: ShadowRoot;
  private container: HTMLDivElement;
  private root: ReactDOM.Root | null = null;

  constructor() {
    super();
    
    // Create shadow DOM
    this.shadow = this.attachShadow({ mode: 'open' });
    
    // Create container for React component
    this.container = document.createElement('div');
    this.container.setAttribute('class', 'voice-assistant-container');
    this.shadow.appendChild(this.container);
    
    // Apply styles
    const style = document.createElement('style');
    style.textContent = `
      .voice-assistant-container {
        position: relative;
        z-index: 9999;
      }
      
      /* Import fonts and base styles */
      @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');
    `;
    this.shadow.appendChild(style);
  }

  connectedCallback() {
    // Get attributes
    const agentId = this.getAttribute('agent-id') || 'bk6zd2EX7Rc7o9LnxEtY';
    
    // Inject Tailwind styles
    const linkElem = document.createElement('link');
    linkElem.setAttribute('rel', 'stylesheet');
    linkElem.setAttribute('href', 'https://cdn.jsdelivr.net/npm/tailwindcss@2.2.19/dist/tailwind.min.css');
    this.shadow.appendChild(linkElem);
    
    // Import additional stylesheets if needed
    const customStylesLink = document.createElement('link');
    customStylesLink.setAttribute('rel', 'stylesheet');
    customStylesLink.setAttribute('href', 'https://elevenlabs.io/convai-widget/styles.css');
    this.shadow.appendChild(customStylesLink);

    // Set up environment variables
    window.env = {
      ...window.env,
      NEXT_PUBLIC_AGENT_ID: agentId,
    };

    // Render React component
    try {
      this.root = createRoot(this.container);
      this.root.render(
        <React.StrictMode>
          <VoiceAssistantWidget embeddedMode={true} />
        </React.StrictMode>
      );
    } catch (error) {
      console.error('Error rendering voice assistant:', error);
    }
  }

  disconnectedCallback() {
    // Clean up React when element is removed
    if (this.root) {
      this.root.unmount();
      this.root = null;
    }
  }
}

// Register the custom element
customElements.define('voice-assistant', VoiceAssistantElement);

export default VoiceAssistantElement;
